@media(min-width: 0px) {

  .userCard {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 200px;
    box-shadow: 0 0 5px 2px grey;
    border-radius: 3px;
    padding: .25rem;
    margin: .5rem;
    min-width: 0;
  }

  .userCard span {
    text-transform: uppercase;
    font-weight: 600;
  }

  .desc {
    padding-top: .5rem;
    text-align: justify;
    font-size: 13px;
  }

}
.usersList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1rem 2rem;
}
.paginationList {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
  height: 70px;
}

.pageNumber {
  cursor: pointer;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid rgba(0,0,0, 0.85);
  border-radius: 20px;
  margin: .25rem;
  font-weight: 600;
}

.pageNumber:hover, .active {
  background-color: rgb(255, 251, 0);
}
main {
  padding-left: 1rem;
  padding-right: 1rem;
}

main h1 {
  position: relative;
  text-align: center;
}

main h1::after {
  display: block;
  content: " ";
  width: 100%;
  height: 2px;
  background-image: -webkit-linear-gradient(left, #343a40 0%, #535353 100%);
  background-image: -webkit-linear-gradient(left, #535353 0%, #343a40 100%);
  background-image: linear-gradient(to right, #535353 0%, #343a40 100%); 
  background-color: grey;
  position: absolute;
  bottom: 0px;
}
footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
  background-color: #111;
  color: #b9b9b9;
}

footer a {
  text-decoration: none;
  color: #b9b9b9;
  margin-top: .25rem;
}

footer a:hover {
  color: white;
}

.footer_contacts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: .5rem 1rem;
}
.App {
  position: relative;
  padding-bottom: 72px;
}

header {
  background-color: rgba(0,0,0, 0.85);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

header img {
  position: absolute;
  left: 1rem;
}

.head_logo {
  width: 168px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

